.title {
  text-align: center;
  font-size: 60px;
  font-weight: 500;
  margin-top: 20px;
  margin-bottom: 30px;
  padding-bottom: 0px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}

.content a {
  width: 170px;
  align-self: center;
  background: #980420;
  color: #EDEBEB;
  font-size: 20px;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  padding: 10px 20px;
  border-radius: 10px;
  border: 2px solid #980420;
  transition: all 0.3s ease;
}

.resumePicture {
  max-width: 1000px;
  width: 90%;
}